<template>
<div id="cell-view">
  <cells :cells-data="cellsExample1"></cells>
  <cells :cells-data="cellsExample2"></cells>
  <cells :cells-data="cellsExample3"></cells>
</div>
</template>

<script>
import Cells from '../../components/weui/cell/cells'

export default {
  props: {
    toast: {
      type: Object
    },
    dialog: {
      type: Object
    },
    actionSheet: {
      type: Object
    }
  },
  data () {
    return {
      cellsExample1: {
        title: '带说明的列表项',
        cellList: [{
          label: '标题文字',
          description: '说明文字'
        }]
      },
      cellsExample2: {
        title: '带图标、说明的列表项',
        cellList: [{
          imgSrc: 'data:image/png;base64,',
          label: '标题文字',
          description: '说明文字'
        }, {
          imgSrc: 'data:image/png;base64,',
          label: '标题文字',
          description: '说明文字'
        }]
      },
      cellsExample3: {
        title: '带图标、说明或者跳转的列表项',
        cellList: [{
          label: '标题文字',
          linkTo: '/'
        }, {
          imgSrc: 'data:image/png;base64,',
          label: '标题文字',
          linkTo: '/'
        }, {
          imgSrc: 'data:image/png;base64,',
          label: '标题文字',
          description: '说明文字',
          linkTo: '/'
        }]
      },
      cellsExample4: {
        title: '带图标、说明的列表项',
        cellList: [
          {
            iconClass: '',
            imgSrc: 'data:image/png;base64,',
            label: '标题文字',
            description: '说明文字',
            linkTo: '/'
          },
          {
            iconClass: '',
            imgSrc: '',
            label: '标题文字',
            description: '说明文字',
            linkTo: '/'
          },
          {
            iconClass: '',
            imgSrc: '',
            label: '标题文字',
            description: '说明文字',
            linkTo: ''
          }
        ]
      }
    }
  },
  components: {
    'cells': Cells
  },
  methods: {
  }
}
</script>



